import React, { useState } from 'react';
import { localStorageApi } from '../services/localStorageApi';

const DataManager: React.FC = () => {
  const [importing, setImporting] = useState(false);
  const [exporting, setExporting] = useState(false);
  const [importResult, setImportResult] = useState<string>('');
  const [importFile, setImportFile] = useState<File | null>(null);
  const [clearing, setClearing] = useState(false);

  // JSON
  const handleExportJSON = async () => {
    setExporting(true);
    try {
      const exportData = await localStorageApi.exportData();
      
      const blob = new Blob([JSON.stringify(exportData, null, 2)], { type: 'application/json' });
      const url = window.URL.createObjectURL(blob);
      const link = document.createElement('a');
      link.href = url;
      
      const timestamp = new Date().toISOString().slice(0, 19).replace(/[:-]/g, '');
      link.download = `racedb_export_${timestamp}.json`;
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
      window.URL.revokeObjectURL(url);
      
      console.log('JSON');
    } catch (error) {
      console.error('JSON:', error);
      alert('JSON');
    }
    setExporting(false);
  };

  // CSV
  const handleExportCSV = async () => {
    setExporting(true);
    try {
      const races = await localStorageApi.getRaces();
      
      // CSV
      const csvHeaders = [
        'ID', '', '', '', '', '', '',
        '', '', '', '', '', '', '', '', '',
        '1', '2', '3'
      ];
      
      let csvContent = csvHeaders.join(',') + '\n';
      
      races.forEach(race => {
        const result1 = race.result ? race.result['1'] || '' : '';
        const result2 = race.result ? race.result['2'] || '' : '';
        const result3 = race.result ? race.result['3'] || '' : '';
        
        race.horses.forEach(horse => {
          const row = [
            race.id,
            race.date,
            race.course,
            race.distance,
            race.surface,
            race.condition,
            race.level || '',
            horse.name,
            horse.frameNumber,
            horse.horseNumber,
            horse.sex,
            horse.age,
            horse.weight,
            horse.jockey,
            horse.odds,
            horse.popularity,
            result1,
            result2,
            result3
          ];
          csvContent += row.map(field => `"${field}"`).join(',') + '\n';
        });
      });
      
      const blob = new Blob(['\uFEFF' + csvContent], { type: 'text/csv;charset=utf-8;' });
      const url = window.URL.createObjectURL(blob);
      const link = document.createElement('a');
      link.href = url;
      
      const timestamp = new Date().toISOString().slice(0, 19).replace(/[:-]/g, '');
      link.download = `racedb_export_${timestamp}.csv`;
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
      window.URL.revokeObjectURL(url);
      
      console.log(' CSV');
    } catch (error) {
      console.error('CSV:', error);
      alert('CSV');
    }
    setExporting(false);
  };

  // 
  const handleFileSelect = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (file) {
      setImportFile(file);
      setImportResult('');
    }
  };

  // JSON
  const handleImportJSON = async () => {
    if (!importFile) {
      alert('');
      return;
    }

    setImporting(true);
    setImportResult('');

    try {
      const fileContent = await importFile.text();
      const importData = JSON.parse(fileContent);
      
      const result = await localStorageApi.importData(importData);
      
      setImportResult(
        `: ${result.imported}, ${result.skipped} (: ${result.total})`
      );
      
      // 
      setImportFile(null);
      const input = document.getElementById('import-file') as HTMLInputElement;
      if (input) input.value = '';
      
    } catch (error: any) {
      console.error(':', error);
      if (error.response?.data?.error) {
        setImportResult(`: ${error.response.data.error}`);
      } else {
        setImportResult('');
      }
    }
    setImporting(false);
  };

  // 
  const handleClearAllData = async () => {
    if (window.confirm(' \n\n\n')) {
      if (window.confirm('')) {
        setClearing(true);
        try {
          await localStorageApi.clearAllData();
          setImportResult('');
          alert('');
        } catch (error) {
          console.error(':', error);
          alert('');
        }
        setClearing(false);
      }
    }
  };

  return (
    <div style={{ margin: '20px 0' }}>
      <h2></h2>
      
      {/*  */}
      <div style={{ marginBottom: '30px', padding: '20px', backgroundColor: '#f8f9fa', borderRadius: '8px' }}>
        <h3 style={{ marginTop: 0 }}></h3>
        <p style={{ color: '#666', marginBottom: '15px' }}>
          
        </p>
        
        <div style={{ display: 'flex', gap: '10px', flexWrap: 'wrap' }}>
          <button
            onClick={handleExportJSON}
            disabled={exporting}
            style={{
              padding: '10px 20px',
              backgroundColor: '#007bff',
              color: 'white',
              border: 'none',
              borderRadius: '5px',
              cursor: exporting ? 'not-allowed' : 'pointer',
              opacity: exporting ? 0.6 : 1
            }}
          >
            {exporting ? '...' : 'JSON'}
          </button>
          
          <button
            onClick={handleExportCSV}
            disabled={exporting}
            style={{
              padding: '10px 20px',
              backgroundColor: '#28a745',
              color: 'white',
              border: 'none',
              borderRadius: '5px',
              cursor: exporting ? 'not-allowed' : 'pointer',
              opacity: exporting ? 0.6 : 1
            }}
          >
            {exporting ? '...' : 'CSV'}
          </button>
        </div>
        
        <div style={{ marginTop: '15px', fontSize: '14px', color: '#666' }}>
          <ul style={{ marginLeft: '20px' }}>
            <li><strong>JSON:</strong> </li>
            <li><strong>CSV:</strong> Excel11</li>
          </ul>
        </div>
      </div>

      {/*  */}
      <div style={{ marginBottom: '30px', padding: '20px', backgroundColor: '#fff3cd', borderRadius: '8px', border: '1px solid #ffeaa7' }}>
        <h3 style={{ marginTop: 0 }}></h3>
        <p style={{ color: '#856404', marginBottom: '15px' }}>
          <strong>:</strong> ID
        </p>
        
        <div style={{ marginBottom: '15px' }}>
          <label htmlFor="import-file" style={{ display: 'block', marginBottom: '5px', fontWeight: 'bold' }}>
            JSON:
          </label>
          <input
            id="import-file"
            type="file"
            accept=".json"
            onChange={handleFileSelect}
            style={{ padding: '5px', border: '1px solid #ccc', borderRadius: '3px', width: '300px' }}
          />
        </div>
        
        {importFile && (
          <div style={{ marginBottom: '15px', padding: '10px', backgroundColor: '#d4edda', borderRadius: '5px' }}>
            <strong>:</strong> {importFile.name} ({Math.round(importFile.size / 1024)}KB)
          </div>
        )}
        
        <button
          onClick={handleImportJSON}
          disabled={importing || !importFile}
          style={{
            padding: '10px 20px',
            backgroundColor: !importFile ? '#6c757d' : '#ffc107',
            color: !importFile ? 'white' : '#212529',
            border: 'none',
            borderRadius: '5px',
            cursor: importing || !importFile ? 'not-allowed' : 'pointer',
            opacity: importing ? 0.6 : 1
          }}
        >
          {importing ? '...' : ''}
        </button>
        
        {importResult && (
          <div style={{ 
            marginTop: '15px', 
            padding: '10px', 
            backgroundColor: importResult.includes('') ? '#f8d7da' : '#d4edda',
            borderRadius: '5px',
            color: importResult.includes('') ? '#721c24' : '#155724'
          }}>
            {importResult}
          </div>
        )}
        
        <div style={{ marginTop: '15px', fontSize: '14px', color: '#856404' }}>
          <strong>:</strong> 
          <ul style={{ marginLeft: '20px', marginTop: '5px' }}>
            <li>JSON</li>
            <li>JSON</li>
          </ul>
        </div>
      </div>

      {/*  */}
      <div style={{ marginBottom: '30px', padding: '20px', backgroundColor: '#fff3cd', borderRadius: '8px', border: '1px solid #ffeaa7' }}>
        <h3 style={{ marginTop: 0, color: '#856404' }}> </h3>
        <p style={{ color: '#856404', marginBottom: '15px' }}>
          <br />
          <strong></strong>
        </p>
        
        <button
          onClick={handleClearAllData}
          disabled={clearing}
          style={{
            padding: '10px 20px',
            backgroundColor: '#dc3545',
            color: 'white',
            border: 'none',
            borderRadius: '5px',
            cursor: clearing ? 'not-allowed' : 'pointer',
            opacity: clearing ? 0.6 : 1,
            fontWeight: 'bold'
          }}
        >
          {clearing ? '...' : ' '}
        </button>
      </div>
    </div>
  );
};

export default DataManager;