import React, { useState } from 'react';
import { localStorageApi, Race } from '../services/localStorageApi';

interface ResultFormProps {
  race: Race;
  onResultUpdated: () => void;
  onCancel: () => void;
}

const ResultForm: React.FC<ResultFormProps> = ({ race, onResultUpdated, onCancel }) => {
  const [result, setResult] = useState<any>(race.result || {});
  const [loading, setLoading] = useState(false);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);

    try {
      await localStorageApi.updateResult(race.id, result);
      onResultUpdated();
    } catch (error) {
      console.error(':', error);
      alert('');
    }
    setLoading(false);
  };

  const handleResultChange = (position: '1' | '2' | '3', value: string) => {
    setResult((prev: any) => ({
      ...prev,
      [position]: value
    }));
  };

  const getHorseDisplayText = (horse: any) => {
    return `${horse.horseNumber} ${horse.name}`;
  };


  return (
    <div style={{ margin: '10px 0', padding: '15px', border: '1px solid #ddd', borderRadius: '5px' }}>
      <h4> - {race.course} {race.level}</h4>
      <form onSubmit={handleSubmit}>
        <div style={{ marginBottom: '10px' }}>
          <label>
            1:
            <select 
              value={result["1"] || ""} 
              onChange={(e) => handleResultChange("1", e.target.value)}
              style={{ marginLeft: '10px', padding: '5px' }}
              required
            >
              <option value=""></option>
              {race.horses.map(horse => (
                <option key={horse.horseNumber} value={horse.name}>
                  {getHorseDisplayText(horse)}
                </option>
              ))}
            </select>
          </label>
        </div>
        
        <div style={{ marginBottom: '10px' }}>
          <label>
            2:
            <select 
              value={result["2"] || ""} 
              onChange={(e) => handleResultChange("2", e.target.value)}
              style={{ marginLeft: '10px', padding: '5px' }}
              required
            >
              <option value=""></option>
              {race.horses.filter(horse => horse.name !== result["1"]).map(horse => (
                <option key={horse.horseNumber} value={horse.name}>
                  {getHorseDisplayText(horse)}
                </option>
              ))}
            </select>
          </label>
        </div>
        
        <div style={{ marginBottom: '10px' }}>
          <label>
            3:
            <select 
              value={result["3"] || ""} 
              onChange={(e) => handleResultChange("3", e.target.value)}
              style={{ marginLeft: '10px', padding: '5px' }}
              required
            >
              <option value=""></option>
              {race.horses.filter(horse => 
                horse.name !== result["1"] && horse.name !== result["2"]
              ).map(horse => (
                <option key={horse.horseNumber} value={horse.name}>
                  {getHorseDisplayText(horse)}
                </option>
              ))}
            </select>
          </label>
        </div>
        
        <div>
          <button type="submit" disabled={loading} style={{ marginRight: '10px', padding: '8px 16px' }}>
            {loading ? '...' : ''}
          </button>
          <button type="button" onClick={onCancel} style={{ padding: '8px 16px' }}>
            
          </button>
        </div>
      </form>
    </div>
  );
};

export default ResultForm;