// API - GitHub Pages
export interface Horse {
  frameNumber: number;
  horseNumber: number;
  name: string;
  sex: string;
  age: number;
  weight: number;
  jockey: string;
  odds: number;
  popularity: number;
}

export interface Race {
  id: string;
  date: string;
  course: string;
  distance: number;
  surface: string;
  condition: string;
  level: string;
  horses: Horse[];
  result?: {
    "1"?: string;
    "2"?: string;
    "3"?: string;
  };
}

const STORAGE_KEY = 'racedb_data';

