import React, { useState } from 'react';
import { localStorageApi } from '../services/localStorageApi';

interface Prediction {
  horse: Horse;
  predictedWinRate: number;
  confidence: number;
  factors: {
    popularity: { rate: number; weight: number };
    jockey: { rate: number; weight: number };
    distance: { rate: number; weight: number };
  };
}

interface Strategy {
  type: string;
  target: string;
  reason: string;
  confidence: number;
  expectedValue: number | string;
  profitMargin?: number;
}

interface StrategyResponse {
  raceAnalysis: {
    predictions: Prediction[];
    topRecommendations: Prediction[];
    averageConfidence: number;
  };
  recommendedStrategies: Strategy[];
  riskLevel: 'low' | 'medium' | 'high';
}

type Horse = {
  frameNumber: number;
  horseNumber: number;
  name: string;
  sex: string;
  age: number;
  weight: number;
  jockey: string;
  odds: number;
  popularity: number;
};

const courses = ['', '', '', '', '', '', '', '', '', ''];

// 
const turfDistances = [
  '1000', '1200', '1400', '1500', '1600', '1800', 
  '2000', '2200', '2300', '2400', '2500', '2600', 
  '3000', '3200', '3400', '3600'
];

// 
const dirtDistances = [
  '1000', '1150', '1200', '1400', '1600', '1700', 
  '1800', '1900', '2100', '2400'
];

// 
const getDistanceOptions = (surface: string) => {
  return surface === '' ? dirtDistances : turfDistances;
};
const surfaces = ['', ''];
const conditions = ['', '', '', ''];
const levels = ['', '', '500', '1000', '1600', '', 'G3', 'G2', 'G1'];

// 
const presetWeights = {
  custom: { name: '', popularity: 0.4, jockey: 0.3, distance: 0.2, base: 0.1 },
  conservative: { name: '', popularity: 0.6, jockey: 0.2, distance: 0.1, base: 0.1 },
  balanced: { name: '', popularity: 0.4, jockey: 0.3, distance: 0.2, base: 0.1 },
  aggressive: { name: '', popularity: 0.2, jockey: 0.4, distance: 0.3, base: 0.1 },
  darkhorse: { name: '', popularity: 0.1, jockey: 0.4, distance: 0.4, base: 0.1 },
  optimal: { name: ' AI', popularity: 0.4, jockey: 0.3, distance: 0.2, base: 0.1 },
  condition: { name: ' ', popularity: 0.4, jockey: 0.3, distance: 0.2, base: 0.1 }
};

const RaceForm = () => {
  const [raceInfo, setRaceInfo] = useState({
    date: '',
    course: '',
    distance: '',
    surface: '',
    condition: '',
    level: ''
  });

  const [horseText, setHorseText] = useState('');
  const [horses, setHorses] = useState<Horse[]>([]);
  const [prediction, setPrediction] = useState<StrategyResponse | null>(null);
  const [predictLoading, setPredictLoading] = useState(false);
  const [customWeights, setCustomWeights] = useState({
    popularity: 0.4,
    jockey: 0.3,
    distance: 0.2,
    base: 0.1
  });
  const [selectedPreset, setSelectedPreset] = useState<keyof typeof presetWeights>('balanced');
  const [showWeightSettings, setShowWeightSettings] = useState(false);
  const [optimizing, setOptimizing] = useState(false);
  const [optimizationResult, setOptimizationResult] = useState<string>('');

  // 
  const extractRaceInfo = (text: string) => {
    // 
    //   
    // 15:45 / 2000m ( B) / : / :
    // 2  6         ()()  15
    // :4300,1700,1100,650,430

    const lines = text.split('\n').map(l => l.trim());
    let course = '';
    let distance = '';
    let surface = '';
    let condition = '';
    let level = '';

    // 215:45 / 2000m ( B) / : / :
    if (lines.length > 1) {
      const parts = lines[1].split('/').map(s => s.trim());
      for (const p of parts) {
        // 
        const surfaceDistanceMatch = p.match(/(|)(\d+)m/);
        if (surfaceDistanceMatch) {
          surface = surfaceDistanceMatch[1] === '' ? '' : surfaceDistanceMatch[1];
          distance = surfaceDistanceMatch[2];
          continue;
        }
        // 
        const conditionMatch = p.match(/:(|||)/);
        if (conditionMatch) {
          condition = conditionMatch[1];
          continue;
        }
      }
    }

    // 32  6         ()()  15
    if (lines.length > 2) {
      const line3 = lines[2];
      // 
      for (const c of courses) {
        if (line3.includes(c)) {
          course = c;
          break;
        }
      }
      // 
      for (const l of levels) {
        if (line3.includes(l)) {
          level = l;
          break;
        }
      }
    }

    // 
    setRaceInfo(prev => {
      const newInfo = { ...prev, course, distance, surface, condition, level };
      // 
      if (surface && distance) {
        const distanceOptions = getDistanceOptions(surface);
        if (!distanceOptions.includes(distance)) {
          // 
          newInfo.distance = '';
        }
      }
      return newInfo;
    });
  };

  // 
  const extractHorseData = () => {
    console.log('');
    const horses: Horse[] = [];

    const lines = horseText.split('\n').map(line => line.trim());

    for (let i = 0; i < lines.length; i++) {
      const frameHorseMatch = lines[i].match(/^(\d+)\s+(\d+)$/);
      if (frameHorseMatch) {
        const frameNumber = parseInt(frameHorseMatch[1]);
        const horseNumber = parseInt(frameHorseMatch[2]);
        
        // --         
        const separatorPattern = /^(--||||||||)$/;
        if (i + 1 < lines.length && separatorPattern.test(lines[i + 1])) {
          const name = lines[i + 2];
          const infoLine = lines[i + 3];
          
          if (name && infoLine) {
            const parts = infoLine.split(/\t+/);

            if (parts.length >= 6) {
              const sexAge = parts[0];
              const weight = parseFloat(parts[1]);
              const jockey = parts[2];
              
              // "454(0)"
              let oddsIndex = 4;
              let popularityIndex = 5;
              
              // parts[4]
              if (parts.length >= 7 && /^\d+\(\S*\)$/.test(parts[4])) {
                // 
                oddsIndex = 5;
                popularityIndex = 6;
              }
              
              const odds = parseFloat(parts[oddsIndex]);
              const popularity = parseInt(parts[popularityIndex]);

              const sex = sexAge.slice(0, 1);
              const age = parseInt(sexAge.slice(1));

              horses.push({
                frameNumber,
                horseNumber,
                name,
                sex,
                age,
                weight,
                jockey,
                odds,
                popularity
              });

              i += 3;
            }
          }
        }
      }
    }

    console.log(':', horses);
    setHorses(horses);
  };

  // 
  const handleHorseChange = (index: number, field: keyof Horse, value: string | number) => {
    setHorses(prev => {
      const newHorses = [...prev];
      if (field === 'frameNumber' || field === 'horseNumber' || field === 'age' || field === 'popularity') {
        newHorses[index][field] = Number(value);
      } else if (field === 'weight' || field === 'odds') {
        newHorses[index][field] = parseFloat(String(value));
      } else {
        newHorses[index][field] = String(value);
      }
      return newHorses;
    });
  };

  // raceInfo
  const handleRaceChange = (key: keyof typeof raceInfo, value: string) => {
    if (key === 'surface') {
      // 
      const newDistanceOptions = getDistanceOptions(value);
      setRaceInfo(prev => ({
        ...prev,
        [key]: value,
        distance: newDistanceOptions.includes(prev.distance) ? prev.distance : ''
      }));
    } else {
      setRaceInfo(prev => ({ ...prev, [key]: value }));
    }
  };

  // 
  const onHorseTextChange = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
    const val = e.target.value;
    setHorseText(val);
    extractRaceInfo(val);
  };

  // 
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault(); //  **** 
    try {
      // 
      const raceData = {
        date: raceInfo.date,
        course: raceInfo.course,
        distance: parseInt(raceInfo.distance),
        surface: raceInfo.surface,
        condition: raceInfo.condition,
        level: raceInfo.level,
        horses: horses
      };

      const response = await localStorageApi.saveRace(raceData);
  
      console.log(' :', response);
      alert('');
      
      // 
      setRaceInfo({
        date: '',
        course: '',
        distance: '',
        surface: '',
        condition: '',
        level: ''
      });
      setHorses([]);
      setHorseText('');
      setPrediction(null);
    } catch (error) {
      console.error(' :', error);
      alert('');
    }
  };

  // AI
  const runPrediction = async () => {
    try {
      setPredictLoading(true);
      const raceData = {
        course: raceInfo.course,
        distance: parseInt(raceInfo.distance),
        surface: raceInfo.surface,
        condition: raceInfo.condition,
        level: raceInfo.level,
        horses: horses
      };

      const prediction = await localStorageApi.predictRace(raceData, customWeights);
      setPrediction({
        raceAnalysis: prediction,
        recommendedStrategies: [],
        riskLevel: prediction.averageConfidence > 0.8 ? 'low' : 
                   prediction.averageConfidence > 0.6 ? 'medium' : 'high'
      });
    } catch (error) {
      console.error(' :', error);
      alert('');
    } finally {
      setPredictLoading(false);
    }
  };

  // 
  const handlePresetChange = async (presetKey: keyof typeof presetWeights) => {
    setSelectedPreset(presetKey);
    
    if (presetKey === 'optimal') {
      // AI
      await optimizeWeights();
    } else if (presetKey === 'condition') {
      // 
      await optimizeConditionWeights();
    } else if (presetKey !== 'custom') {
      const preset = presetWeights[presetKey];
      setCustomWeights({
        popularity: preset.popularity,
        jockey: preset.jockey,
        distance: preset.distance,
        base: preset.base
      });
      setOptimizationResult('');
    }
  };

  // AI
  const optimizeWeights = async () => {
    setOptimizing(true);
    try {
      const optimalWeights = await localStorageApi.calculateOptimalWeights();
      setCustomWeights(optimalWeights);
      
      const result = ` AI\n: ${(optimalWeights.popularity * 100).toFixed(1)}% | : ${(optimalWeights.jockey * 100).toFixed(1)}% | : ${(optimalWeights.distance * 100).toFixed(1)}% | : ${(optimalWeights.base * 100).toFixed(1)}%`;
      setOptimizationResult(result);
      
      console.log(' AI:', optimalWeights);
    } catch (error) {
      console.error(':', error);
      setOptimizationResult(' ');
    }
    setOptimizing(false);
  };

  // 
  const optimizeConditionWeights = async () => {
    setOptimizing(true);
    try {
      // 
      if (!raceInfo.surface || !raceInfo.distance || !raceInfo.course) {
        setOptimizationResult(' ');
        setOptimizing(false);
        return;
      }

      const raceConditions = {
        surface: raceInfo.surface,
        distance: parseInt(raceInfo.distance),
        course: raceInfo.course
      };

      const conditionWeights = await localStorageApi.calculateConditionBasedWeights(raceConditions);
      setCustomWeights(conditionWeights);
      
      const result = ` \\n${raceInfo.surface} ${raceInfo.distance}m (${raceInfo.course}) \\n: ${(conditionWeights.popularity * 100).toFixed(1)}% | : ${(conditionWeights.jockey * 100).toFixed(1)}% | : ${(conditionWeights.distance * 100).toFixed(1)}% | : ${(conditionWeights.base * 100).toFixed(1)}%\\n\\n (F12)`;
      setOptimizationResult(result);
      
      console.log(' :', conditionWeights);
    } catch (error) {
      console.error(':', error);
      setOptimizationResult(' ');
    }
    setOptimizing(false);
  };

  // 
  const handleWeightChange = (factor: string, value: number) => {
    setSelectedPreset('custom'); // 
    setCustomWeights(prev => ({
      ...prev,
      [factor]: value
    }));
  };

  // 
  const resetWeights = () => {
    setSelectedPreset('balanced');
    setCustomWeights({
      popularity: 0.4,
      jockey: 0.3,
      distance: 0.2,
      base: 0.1
    });
  };

  // 
  const getRiskColor = (level: string) => {
    switch (level) {
      case 'low': return '#28a745';
      case 'medium': return '#ffc107';
      case 'high': return '#dc3545';
      default: return '#6c757d';
    }
  };
  return (
    <form onSubmit={handleSubmit} style={{ maxWidth: 900, margin: 'auto', padding: 20 }}>
      <h2></h2>

      <div style={{ marginBottom: '15px' }}>
        <label>: </label>
        <input
          type="date"
          value={raceInfo.date}
          onChange={(e) => handleRaceChange('date', e.target.value)}
          style={{ marginLeft: '10px', padding: '5px' }}
          required
        />
      </div>

      <div style={{ marginBottom: '15px' }}>
        <label style={{ display: 'block', marginBottom: '5px', fontWeight: 'bold' }}>: </label>
        <div>
          {courses.map(c => (
            <button
              type="button"
              key={c}
              onClick={() => handleRaceChange('course', c)}
              style={{
                margin: 4,
                padding: '6px 12px',
                backgroundColor: raceInfo.course === c ? '#007bff' : 'white',
                color: raceInfo.course === c ? 'white' : 'black',
                border: '1px solid #ccc',
                borderRadius: '4px',
                cursor: 'pointer'
              }}
            >
              {c}
            </button>
          ))}
        </div>
      </div>

      <div style={{ marginBottom: '15px' }}>
        <label style={{ display: 'block', marginBottom: '5px', fontWeight: 'bold' }}>: </label>
        <div>
          {surfaces.map(s => (
            <button
              type="button"
              key={s}
              onClick={() => handleRaceChange('surface', s)}
              style={{
                margin: 4,
                padding: '6px 12px',
                backgroundColor: raceInfo.surface === s ? '#007bff' : 'white',
                color: raceInfo.surface === s ? 'white' : 'black',
                border: '1px solid #ccc',
                borderRadius: '4px',
                cursor: 'pointer'
              }}
            >
              {s}
            </button>
          ))}
        </div>
      </div>

      <div style={{ marginBottom: '15px' }}>
        <label style={{ display: 'block', marginBottom: '5px', fontWeight: 'bold' }}>: </label>
        <div style={{ display: 'flex', flexWrap: 'wrap', gap: '4px' }}>
          {getDistanceOptions(raceInfo.surface).map(d => (
            <button
              type="button"
              key={d}
              onClick={() => handleRaceChange('distance', d)}
              style={{
                padding: '4px 8px',
                backgroundColor: raceInfo.distance === d ? '#007bff' : 'white',
                color: raceInfo.distance === d ? 'white' : 'black',
                border: '1px solid #ccc',
                borderRadius: '3px',
                cursor: 'pointer',
                fontSize: '14px'
              }}
            >
              {d}m
            </button>
          ))}
        </div>
        {raceInfo.surface && (
          <small style={{ color: '#666', display: 'block', marginTop: '5px' }}>
            {raceInfo.surface === '' ? '' : ''}
          </small>
        )}
      </div>

      <div style={{ marginBottom: '15px' }}>
        <label style={{ display: 'block', marginBottom: '5px', fontWeight: 'bold' }}>: </label>
        <div>
          {conditions.map(c => (
            <button
              type="button"
              key={c}
              onClick={() => handleRaceChange('condition', c)}
              style={{
                margin: 4,
                padding: '6px 12px',
                backgroundColor: raceInfo.condition === c ? '#007bff' : 'white',
                color: raceInfo.condition === c ? 'white' : 'black',
                border: '1px solid #ccc',
                borderRadius: '4px',
                cursor: 'pointer'
              }}
            >
              {c}
            </button>
          ))}
        </div>
      </div>

      <div style={{ marginBottom: '20px' }}>
        <label style={{ display: 'block', marginBottom: '5px', fontWeight: 'bold' }}>: </label>
        <div>
          {levels.map(l => (
            <button
              type="button"
              key={l}
              onClick={() => handleRaceChange('level', l)}
              style={{
                margin: 4,
                padding: '6px 12px',
                backgroundColor: raceInfo.level === l ? '#007bff' : 'white',
                color: raceInfo.level === l ? 'white' : 'black',
                border: '1px solid #ccc',
                borderRadius: '4px',
                cursor: 'pointer'
              }}
            >
              {l}
            </button>
          ))}
        </div>
      </div>

      <h2></h2>
      <textarea
        rows={10}
        style={{ width: '100%', fontFamily: 'monospace' }}
        value={horseText}
        onChange={onHorseTextChange}
        placeholder="netkeiba"
      />

      <button type="button" onClick={extractHorseData} style={{ marginTop: 10, padding: '6px 12px' }}>
        
      </button>

      {horses.length > 0 && (
        <>
          <h2></h2>
          <table border={1} cellPadding={4} style={{ borderCollapse: 'collapse', width: '100%' }}>
            <thead>
              <tr>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th>(kg)</th>
                <th></th>
                <th></th>
                <th></th>
              </tr>
            </thead>
            <tbody>
              {horses.map((h, i) => (
                <tr key={`${h.frameNumber}-${h.horseNumber}`}>
                  <td>
                    <input
                      type="number"
                      value={h.frameNumber}
                      onChange={e => handleHorseChange(i, 'frameNumber', e.target.value)}
                      style={{ width: '50px' }}
                    />
                  </td>
                  <td>
                    <input
                      type="number"
                      value={h.horseNumber}
                      onChange={e => handleHorseChange(i, 'horseNumber', e.target.value)}
                      style={{ width: '50px' }}
                    />
                  </td>
                  <td>
                    <input
                      type="text"
                      value={h.name}
                      onChange={e => handleHorseChange(i, 'name', e.target.value)}
                    />
                  </td>
                  <td>
                    <select
                      value={h.sex}
                      onChange={e => handleHorseChange(i, 'sex', e.target.value)}
                    >
                      <option value=""></option>
                      <option value=""></option>
                      <option value=""></option>
                    </select>
                  </td>
                  <td>
                    <input
                      type="number"
                      value={h.age}
                      onChange={e => handleHorseChange(i, 'age', e.target.value)}
                      style={{ width: '40px' }}
                    />
                  </td>
                  <td>
                    <input
                      type="number"
                      step="0.1"
                      value={h.weight}
                      onChange={e => handleHorseChange(i, 'weight', e.target.value)}
                      style={{ width: '60px' }}
                    />
                  </td>
                  <td>
                    <input
                      type="text"
                      value={h.jockey}
                      onChange={e => handleHorseChange(i, 'jockey', e.target.value)}
                    />
                  </td>
                  <td>
                    <input
                      type="number"
                      step="0.1"
                      value={h.odds}
                      onChange={e => handleHorseChange(i, 'odds', e.target.value)}
                      style={{ width: '60px' }}
                    />
                  </td>
                  <td>
                    <input
                      type="number"
                      value={h.popularity}
                      onChange={e => handleHorseChange(i, 'popularity', e.target.value)}
                      style={{ width: '40px' }}
                      min="1"
                      max={horses.length}
                      title={`1${horses.length}`}
                    />
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </>
      )}

      {/*  */}
      {horses.length > 0 && raceInfo.course && raceInfo.distance && raceInfo.surface && (
        <div style={{ marginTop: '30px', backgroundColor: '#f8f9fa', padding: '20px', borderRadius: '8px' }}>
          <h2> AI</h2>
          <p>: {raceInfo.course} {raceInfo.distance}m {raceInfo.surface} ({horses.length})</p>
          
          {/* UI */}
          <div style={{ marginBottom: '15px' }}>
            <button
              type="button"
              onClick={() => setShowWeightSettings(!showWeightSettings)}
              style={{
                backgroundColor: '#6c757d',
                color: 'white',
                padding: '6px 12px',
                border: 'none',
                borderRadius: '4px',
                cursor: 'pointer',
                marginRight: '10px',
                fontSize: '14px'
              }}
            >
                {showWeightSettings ? '' : ''}
            </button>
            
            {showWeightSettings && (
              <div style={{ 
                backgroundColor: 'white', 
                padding: '15px', 
                border: '1px solid #dee2e6', 
                borderRadius: '6px', 
                marginTop: '10px' 
              }}>
                <h4></h4>
                
                {/*  */}
                <div style={{ marginBottom: '20px', padding: '15px', backgroundColor: '#e3f2fd', borderRadius: '6px' }}>
                  <h5 style={{ margin: '0 0 10px 0', color: '#1976d2' }}> </h5>
                  <select
                    value={selectedPreset}
                    onChange={(e) => handlePresetChange(e.target.value as keyof typeof presetWeights)}
                    style={{
                      padding: '8px 12px',
                      fontSize: '14px',
                      borderRadius: '4px',
                      border: '1px solid #ddd',
                      marginBottom: '10px',
                      minWidth: '200px'
                    }}
                  >
                    {Object.entries(presetWeights).map(([key, preset]) => (
                      <option key={key} value={key}>{preset.name}</option>
                    ))}
                  </select>
                  
                  <div style={{ fontSize: '12px', color: '#666', marginTop: '5px' }}>
                    {selectedPreset === 'conservative' && ' '}
                    {selectedPreset === 'balanced' && ' '}
                    {selectedPreset === 'aggressive' && ' '}
                    {selectedPreset === 'darkhorse' && ' '}
                    {selectedPreset === 'optimal' && ' '}
                    {selectedPreset === 'condition' && ' '}
                    {selectedPreset === 'custom' && ' '}
                  </div>
                  
                  {/* AI */}
                  {optimizing && (
                    <div style={{ 
                      marginTop: '10px', 
                      padding: '10px', 
                      backgroundColor: '#fff3cd', 
                      borderRadius: '4px',
                      fontSize: '14px',
                      color: '#856404'
                    }}>
                      {selectedPreset === 'condition' 
                        ? ' ... '
                        : ' AI... '
                      }
                    </div>
                  )}
                  
                  {optimizationResult && (
                    <div style={{ 
                      marginTop: '10px', 
                      padding: '10px', 
                      backgroundColor: optimizationResult.includes('') ? '#f8d7da' : '#d4edda', 
                      borderRadius: '4px',
                      fontSize: '12px',
                      color: optimizationResult.includes('') ? '#721c24' : '#155724',
                      whiteSpace: 'pre-line'
                    }}>
                      {optimizationResult}
                    </div>
                  )}
                </div>

                <div style={{ display: 'grid', gridTemplateColumns: 'repeat(auto-fit, minmax(200px, 1fr))', gap: '10px' }}>
                  {Object.entries(customWeights).map(([factor, weight]) => (
                    <div key={factor}>
                      <label style={{ display: 'block', fontSize: '14px', marginBottom: '4px' }}>
                        {factor === 'popularity' ? '' : 
                         factor === 'jockey' ? '' : 
                         factor === 'distance' ? '' : ''}: {(weight * 100).toFixed(0)}%
                      </label>
                      <input
                        type="range"
                        min="0"
                        max="1"
                        step="0.05"
                        value={weight}
                        onChange={(e) => handleWeightChange(factor, parseFloat(e.target.value))}
                        style={{ width: '100%' }}
                      />
                    </div>
                  ))}
                </div>
                <div style={{ marginTop: '10px', fontSize: '12px', color: '#6c757d' }}>
                  : {(Object.values(customWeights).reduce((sum, w) => sum + w, 0) * 100).toFixed(0)}% 
                  ()
                </div>
                <button
                  type="button"
                  onClick={resetWeights}
                  style={{
                    backgroundColor: '#dc3545',
                    color: 'white',
                    padding: '4px 8px',
                    border: 'none',
                    borderRadius: '4px',
                    cursor: 'pointer',
                    marginTop: '8px',
                    fontSize: '12px'
                  }}
                >
                  
                </button>
              </div>
            )}
          </div>
          
          <button
            type="button"
            onClick={runPrediction}
            disabled={predictLoading}
            style={{
              backgroundColor: '#28a745',
              color: 'white',
              padding: '10px 20px',
              border: 'none',
              borderRadius: '6px',
              cursor: predictLoading ? 'not-allowed' : 'pointer',
              marginBottom: '15px',
              fontSize: '16px'
            }}
          >
            {predictLoading ? '...' : ' '}
          </button>

          {prediction && (
            <div style={{ marginTop: '15px' }}>
              <div style={{ 
                backgroundColor: getRiskColor(prediction.riskLevel),
                color: 'white',
                padding: '8px 12px',
                borderRadius: '4px',
                display: 'inline-block',
                marginBottom: '15px'
              }}>
                : {prediction.riskLevel.toUpperCase()}
              </div>

              <h3> </h3>
              <div style={{ marginBottom: '20px' }}>
                {prediction.raceAnalysis.topRecommendations.map((pred, index) => (
                  <div 
                    key={index}
                    style={{
                      backgroundColor: index === 0 ? '#fff3cd' : '#f8f9fa',
                      padding: '12px',
                      margin: '5px 0',
                      borderRadius: '6px',
                      border: index === 0 ? '2px solid #ffc107' : '1px solid #dee2e6'
                    }}
                  >
                    <div style={{ fontWeight: 'bold', fontSize: '16px' }}>
                      {index + 1}: {pred.horse.name} ({pred.horse.jockey})
                    </div>
                    <div style={{ fontSize: '14px', color: '#6c757d', marginTop: '4px' }}>
                      : {(pred.predictedWinRate * 100).toFixed(1)}% | 
                      : {(pred.confidence * 100).toFixed(0)}% | 
                      : {pred.horse.odds} | 
                      {pred.horse.popularity}
                    </div>
                  </div>
                ))}
              </div>

              <h3> </h3>
              {prediction.recommendedStrategies.length > 0 ? (
                <div>
                  {prediction.recommendedStrategies.map((strategy, index) => (
                    <div 
                      key={index}
                      style={{
                        backgroundColor: '#d4edda',
                        border: '1px solid #c3e6cb',
                        padding: '12px',
                        margin: '8px 0',
                        borderRadius: '6px'
                      }}
                    >
                      <div style={{ fontWeight: 'bold', color: '#155724', fontSize: '16px' }}>
                         {strategy.type}: {strategy.target}
                      </div>
                      <div style={{ fontSize: '14px', margin: '4px 0', color: '#155724' }}>
                         {strategy.reason}
                      </div>
                      <div style={{ fontSize: '12px', color: '#6c757d' }}>
                        : {(strategy.confidence * 100).toFixed(0)}% | 
                        : {typeof strategy.expectedValue === 'number' ? 
                          strategy.expectedValue.toFixed(2) : strategy.expectedValue}
                        {strategy.profitMargin && ` (${strategy.profitMargin >= 0 ? '+' : ''}${strategy.profitMargin}%)`}
                      </div>
                    </div>
                  ))}
                </div>
              ) : (
                <div style={{ color: '#6c757d', fontStyle: 'italic', padding: '10px' }}>
                  
                </div>
              )}
            </div>
          )}
        </div>
      )}

      <button type="submit" style={{ marginTop: 20, padding: '8px 16px' }}>
        
      </button>
    </form>
  );
};

export default RaceForm;
